export default "import React from 'react';\nimport {\n    ComponentsRepo,\n    AutoFields,\n    AutoItems,\n    AutoHeaders\n} from '@autoviews/core';\nimport {\n    Table,\n    TableContainer,\n    Paper,\n    TableHead,\n    TableRow,\n    TableCell,\n    TableBody\n} from '@mui/material';\n\nexport const repo = new ComponentsRepo('MyTableRepo')\n    .register('array', {\n        name: 'MyTableComponent',\n        component: props => {\n            return (\n                <TableContainer\n                    component={Paper}\n                    sx={{margin: '10px'}}\n                >\n                    <Table>\n                        <TableHead>\n                            <TableRow>\n                                <AutoHeaders\n                                    {...props}\n                                    path=\"/items\"\n                                >\n                                    {headerProps => (\n                                        <AutoItems {...headerProps} />\n                                    )}\n                                </AutoHeaders>\n                            </TableRow>\n                        </TableHead>\n                        <TableBody>\n                            <AutoItems {...props} />\n                        </TableBody>\n                    </Table>\n                </TableContainer>\n            );\n        }\n    })\n    .register('object', {\n        name: 'MyTableRow',\n        component: props => (\n            <TableRow>\n                <AutoFields {...props} />\n            </TableRow>\n        )\n    })\n    .register('string', {\n        name: 'MyStringComponent',\n        component: props => <span>{props.data}</span>\n    })\n    .register('number', {\n        name: 'MyNumberComponent',\n        component: props => <span>{props.data}</span>\n    })\n    .register('boolean', {\n        name: 'MyBooleanComponent',\n        component: props => {\n            const isOnline = props.data;\n            return (\n                <span style={{color: isOnline ? 'green' : 'red'}}>\n                    {isOnline ? 'online' : 'offline'}\n                </span>\n            );\n        }\n    })\n    .addWrapper(item => <TableCell>{item}</TableCell>, {\n        include: [\n            'MyStringComponent',\n            'MyBooleanComponent',\n            'MyNumberComponent'\n        ]\n    });\n";